	module.exports = {"data":{"post":{"html":"<p>Strange, with Mozilla 0.9.9 I cannot flip my page... the http proxy says it cannot process my request.</p>\n<p>The Christopher crisis yesterday had to do with the volume of liquids. He wanted to drink his apple juice from a bottle rather than a plastic cup, and was extremely upset that 0.3 l juice from the cup did not fit into the 0.25 l bottle. He wanted all the juice in the bottle and none in the cup. It took me 15 minutes to come up with the idea to let the bottle overflow (into a bowl), so that that bottle was full and the cup was empty.</p>","fields":{"title":"Christopher Crisis","date":"14 Mar 2002","url":"https://papascott.de/2002-03-14-christopher-crisis/"}}},"pathContext":{"slug":"/2002-03-14-christopher-crisis/","prev":{"fields":{"title":"First Steps","date":"13 Mar 2002","slug":"/2002-03-13-first-steps/"}},"next":{"fields":{"title":"Flyte Tyme","date":"16 Mar 2002","slug":"/2002-03-16-flyte-tyme/"}}}}

